<template>
  <div id="app">
    <img class="mobile-icon" src="static/menu.png" @click="show = !show">
    <transition name="sidebar">
      <div class="mobile-nav-div" v-if="show">
        <nav class="mobile-nav" v-if="show">
          <ul>
            <li><router-link to="/timeline" @click="show = false"><a href="#">主页</a></router-link></li>
            <li><router-link to="/filters" @click="show = false"><a href="#">图片</a></router-link></li>
            <li><router-link to="/filters" @click="show = false"><a href="#">视频</a></router-link></li>
            <li><router-link to="/sns" @click="show = false"><a href="#">官方SNS合辑</a></router-link></li>
            <li><router-link to="/aboutBona" @click="show = false"><a href="#">关于苞娜</a></router-link></li>
            <li><router-link to="/aboutUs" @click="show = false"><a href="#">关于我们</a></router-link></li>
            <li><router-link to="/links" @click="show = false"><a href="#">友情链接</a></router-link></li>
            <li><router-link to="/updateLog" @click="show = false"><a href="#">更新日志</a></router-link></li>
          </ul>
        </nav> 
      </div>
    </transition>
    <div id="banner" @click="show = false">
      <div id="banner-bg"></div>     
    </div>
    <div id="main" @click="show = false">
      <div id="nav">
        <!-- nav.vue -->
        <Navigation></Navigation>
      </div>
      <div id="timeline">
        <transition name="fade">
          <router-view></router-view>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import Timeline from './components/timeline.vue'
import Navigation from './components/nav.vue'
import AboutBona from './components/aboutBona.vue'
import Filters from './components/filters.vue'
import AboutUs from './components/aboutUs.vue'
import Links from './components/links.vue'
import UpdateLog from './components/updateLog.vue'
import SNS from './components/sns.vue'

export default {
  name: 'app',
  data () {
    return {
      show: false
    }
  },
  components: {
    Timeline,
    Navigation,
    AboutBona,
    Filters,
    AboutUs,
    Links,
    UpdateLog,
    SNS
  }
}
</script>

<style>
  #banner-bg {
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 250px;
    background: url(../static/banner.png) center;
    background-size: cover;
  }
  #main {
    margin-top: 250px;
    display: -moz-box; 
    display: -webkit-box; 
    display: box; 
    width: 100%;
    height: 100%;
  }
  #nav {
    position: fixed;
    height: 100%;
    width: 200px;
    background-color: #bcdcbf;
    margin-top: -250px;
    padding-top: 250px;
    z-index: 1;
  }
  #timeline {
    margin-left: 220px;
    position: relative;
  }
  /*
  #bg {
    position: relative;
    -moz-box-flex: 1; 
    -webkit-box-flex: 1; 
    box-flex: 1;
    height: 800px;
    background: url(../static/side.png) center;
    background-size: cover;
  }
  */
  .mobile-icon {
    display: none;
  }
  
  .mobile-icon {
    position: fixed;
    margin-top: -250px;
    z-index: 5;
    width: 30px;
    height: 30px;
  }
  .mobile-nav-div {
    position: fixed;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto; 
    z-index: 4;
    margin-top: -250px;
  }
  .mobile-nav {
    position: fixed;
    padding: 20px;
    padding-top: 100px;
    display: block;
    position: relative;
    -moz-box-flex: 1; 
    -webkit-box-flex: 1; 
    box-flex: 1;
    background-color: #bcdcbf;
    height: 100%;
  }
  .mobile-nav ul {
    margin: 0;
    padding: 0;
    text-align: center;
  }
  .mobile-nav li {
    margin: 0 5px 10px 0;
    padding: 10px;
    list-style: none;
  }
  .mobile-nav a {
    padding: 3px 12px;
    text-decoration: none;
    color: #999;
    line-height: 100%;
  }
  .mobile-nav a:hover {
    color: #000;
  }
  .sidebar-enter-active, .sidebar-leave-active {
    -webkit-transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .sidebar-enter, .sidebar-leave-active {
    -webkit-transform: translateX(-200px); 
    transform: translateX(-200px); 
  }
  @media screen and (max-width: 600px) {
    .mobile-icon {
      display: block;
    }
    .mobile-nav-div {
      transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }
    #nav {
      display: none;
    }
    #timeline {
      margin-left: 20px;
    }
  }
  @media screen and (min-width: 600px) {
    .mobile-nav-div {
      -webkit-transition: all .1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
      transition: all .1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
      -webkit-transform: translateX(-210px);
      transform: translateX(-210px);
    }
  }
  .fade-enter-active {
    -webkit-transition: opacity .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    transition: opacity .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .fade-enter, .fade-leave-active {
    opacity: 0
  }
</style>



